import request from '@/utils/request'

/**
 * 
 * */
export function getCountryCodes() {
	return request({
		url: '/countryCodes',
		method: 'get',
	})
}
/**
 * 
 * */
export function getVerificationCodes(data) {
	return request({
		url: '/verificationCodes',
		method: 'post',
		data
	})
}

/*export function login(data) {
	return request({
		url: '/verificationCodes/authorizations',
		method: 'post',
		data
	})
}*/
export function login(data) {
	return request({
		url: '/authorizations/login',
		method: 'post',
		data
	})
}
/**
 * 
 * */
export function sendCode(data) {
	return request({
		url: '/send/code',
		method: 'post',
		data
	})
}
/**
 * 
 * */
export function updatePwd(data) {
	return request({
		url: '/modify/pwd',
		method: 'post',
		data
	})
}

/**
 * 
 * */
export function queryPwd(data) {
	return request({
		url: '/query/pwd',
		method: 'post',
		data
	})
}
/**
 * 
 * */
export function getInfo() {
	return request({
		url: '/user',
		method: 'get',
	})
}

/**
 * 
 * */
export function setUserInfo(data) {

	return request({
		url: '/user',
		method: 'patch',
		data: data,
	})
}
/**
 * 
 * */
export function uploadImage(data) {
	return request({
		url: '/images',
		method: 'post',
		data,
		headers: {'Content-Type': 'application/form-data'},
	})
}
/**
 *  Token
 * */
export function reLogin() {
	return request({
		url: '/authorizations/current',
		method: 'put'
	})
}
/**
 * 
 * */
export function logout() {
	return request({
		url: '/authorizations/current',
		method: 'delete'
	})
}

/**
 * 
 * */
export function recordOP(data) {
	return request({
		url: '/user/operations/current',
		method: 'post',
		data
	})
}

/**
 * Apps
 * */
export function getAppsVersion() {
	return request({
		url: '/appsVersion',
		method: 'get',
	})
}